// ====================  Default  =========================

/*! Defaults */
body {
  color: $main-color;
  font-family: $default-font;

  @include rfs(24, 16);
}

// sticky footer (always at the bottom of page)

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;

}

.content {
  flex: 1 0 auto;
}

.footer {
  flex: 0 0 auto;
}

.container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin: 0 auto;
  padding: 0 15px;
  max-width: 1170px;
  width: 100%;
}

.col {
  width: 50%;
  padding: 0 15px;
  display: flex;
  justify-content: center;
  align-items: center;
}

/*! Defaults End */

// =====================  modules  ========================

@import "header/header";
@import "content/content";
@import "footer/footer";

// =====================  Media  ==========================

@media screen and (max-width: 1199px) {

  .header,
  .footer {
    background: #ff0022;
  }
}

@media screen and (max-width: 991px) {

  .header,
  .footer {
    background: #fbff00;
  }
}

@media screen and (max-width: 767px) {

  .header,
  .footer {
    background: #2bff00;
  }
}

@media screen and (max-width: 575px) {

  .header,
  .footer {
    background: #00ccff;
  }
}

@media screen and (max-width: 319px) {

  .header,
  .footer {
    background: #ff0080;
  }
}
